import React, { useState, useEffect } from 'react';
import '../styles/LecturerPage.css';
import lecturerData from '../database/lecturer2.json';
import lecturers from '../database/lecturers.json';
import { useNavigate } from 'react-router-dom';


function Lecturer1() {
  const [lectures, setLectures] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [modalDescription, setModalDescription] = useState('');

  const navigate = useNavigate(); 

  useEffect(() => {
    setLectures(lecturerData);
  }, []);

  const openModal = (description) => {
    setModalDescription(description);
    setModalOpen(true);
  };

  const closeModal = () => {
    setModalOpen(false);
  };

  return (
    <div className='lecturer-page'>
      <header>
        <h1>{lecturers[1]?.name}</h1>
        <p>{lecturers[1]?.fullBio}</p>
        <button onClick={() => navigate('/')} className="back-button">
          Back to Home
        </button>
      </header>

      <table>
        <thead>
          <tr>
            <th>Predavanja PDF Download</th>
            <th>YouTube Link</th>
            <th>Opis</th>
            <th>Naziv</th>
          </tr>
        </thead>
        <tbody>
          {lectures.map(lecture => (
            <tr key={lecture.id}>
              <td>
                <a href={lecture.pdfLink} download>Download PDF</a>
              </td>
              <td>
                <a href={lecture.youtubeLink} target="_blank" rel="noopener noreferrer">Watch</a>
              </td>
              <td>
                <button onClick={() => openModal(lecture.description)}>View Description</button>
              </td>
              <td>{lecture.title}</td>
            </tr>))}
        </tbody>
      </table>

      {modalOpen && (
        <div className="modal" onClick={closeModal}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <span className="close" onClick={closeModal}>&times;</span>
            <p>{modalDescription}</p>
          </div>
        </div>
      )}
    </div>
  );
}

export default Lecturer1;
